import React from 'react';
import './App.css';



class GlobalindexList extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {

    const{
      investingdata
    }=this.props;

     
    return (
          <table>
            <thead>
              <tr>
                <th>Name</th>
                <th>Change</th>
              </tr>
            </thead>

            <tbody>
            
             { investingdata.index.map((data,index)=> <WatchListRow key={data[0]+index} investingdata={investingdata} data={data} />)}
             
            </tbody>
        </table>
    )
  }

}


export default GlobalindexList;

class WatchListRow extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {

    const{
      data
    }=this.props;


    let changeClass =data[13].startsWith("-") ?'red':'green';
    
      return (
        <tr>
          <td>{data[0]}</td> 
          <td className={changeClass} >{data[13]}</td>            
         </tr>
      );

  }

}